import React, { useState } from "react";
import { __SECRET_INTERNALS_DO_NOT_USE_OR_YOU_WILL_BE_FIRED } from "react";
import "./QuizMain.css";

export default function App() {
  const questions = [
    {
      questionText:
        "É a distância, em graus, de qualquer ponto da superfície terrestre em relação à Linha do Equador. Quanto menor for ela, maior será a temperatura da região. As freses anteriores se referem à: ",
      answerOptions: [
        { answerText: "Longitude", isCorrect: false },
        { answerText: "Latitude", isCorrect: true },
        { answerText: "Altitude", isCorrect: false },
        { answerText: "Alternativas “a” e “b”", isCorrect: false },
      ],
    },
    {
      questionText:
        "A partir de seus conhecimentos em geografia, assinale a alternativa que expresse corretamente uma informação sobre altitude.",
      answerOptions: [
        {
          answerText:
            "A altitude influencia pouco na temperatura. Altas altitudes, por exemplo, apresentam temperaturas mais altas.",
          isCorrect: false,
        },
        {
          answerText:
            "O nível do mar é utilizado como referência para sua medição",
          isCorrect: true,
        },
        {
          answerText: "Quanto maior for a altitude, menos rarefeito será o ar",
          isCorrect: false,
        },
        {
          answerText: "Alta altitude é sinônimo de maior pressão atmosférica",
          isCorrect: false,
        },
      ],
    },
    {
      questionText:
        "Pressão atmosférica é a força causada pelo ar sobre a superfície terrestre. Em relação a ela, assinale a alternativa INCORRETA.",
      answerOptions: [
        {
          answerText:
            "Regiões com temperaturas baixas apresentam a pressão atmosférica alta",
          isCorrect: false,
        },
        {
          answerText: "Quanto maior a altitude, menor a pressão atmosférica",
          isCorrect: false,
        },
        {
          answerText:
            "Regiões situadas na região equatorial apresentam a pressão atmosférica alta",
          isCorrect: true,
        },
        {
          answerText: "Quanto menor a latitude, menor a pressão atmosférica",
          isCorrect: false,
        },
      ],
    },
    {
      questionText:
        "Os continentes, quando sofrem influência da continentalidade, se aquecem rapidamente durante o dia e se esfriam rapidamente durante a noite. Essa situação, ocasiona:",
      answerOptions: [
        {
          answerText: "Temperaturas constantes durante todo o tempo",
          isCorrect: false,
        },
        { answerText: "Alta amplitude térmica", isCorrect: true },
        { answerText: "Baixa amplitude térmica", isCorrect: false },
        { answerText: "N.D.A.", isCorrect: false },
      ],
    },
    {
      questionText:
        'Marque a alternativa com informações corretas sobre o relevo "depressão"',
      answerOptions: [
        { answerText: "Se localizam no fundo dos oceanos", isCorrect: false },
        {
          answerText:
            "Tende a apresentar baixas temperaturas e maior raridade de oxigênio",
          isCorrect: false,
        },
        { answerText: "Baixas latitudes", isCorrect: false },
        {
          answerText:
            "É um relevo muito mais baixo em relação aos relevos que o circulam",
          isCorrect: true,
        },
      ],
    },
    {
      questionText:
        'Qual das alternativas corresponde com a precipitação descrita a seguir: "Precipitação que acontece com o encontro de uma massa de ar com um obstáculo (relevo) alto"',
      answerOptions: [
        { answerText: "Chuva Orográfica", isCorrect: true },
        { answerText: "Chuva Convectiva", isCorrect: false },
        { answerText: "Chuva Frontal", isCorrect: false },
        { answerText: "N.D.A.", isCorrect: false },
      ],
    },
    {
      questionText:
        "Qual é a única massa de ar atuante no Brasil que é úmida e continental ao mesmo tempo?",
      answerOptions: [
        { answerText: "Massa Tropical Atlântica", isCorrect: false },
        { answerText: "Massa Polar", isCorrect: false },
        { answerText: "Massa Equatorial Continental", isCorrect: true },
        { answerText: "Massa Equatorial Atlântica", isCorrect: false },
      ],
    },
    {
      questionText: "Em que a vegetação influencia no clima?",
      answerOptions: [
        {
          answerText:
            "Evita que os raios solares incidam diretamente no solo, diminuindo a temperatura",
          isCorrect: true,
        },
        {
          answerText:
            "Quanto maior e mais densa a vegetação, maior a temperatura da região por causa da irradiação dos raios solares",
          isCorrect: false,
        },
        {
          answerText: "Torna a região seca, pois absorve toda a umidade",
          isCorrect: false,
        },
        {
          answerText: "Diminui a temperatura da região através da capilaridade",
          isCorrect: false,
        },
      ],
    },
    {
      questionText:
        "Marque a alternativa que cita respectivamente uma massa de ar úmida e uma massa de ar seca.",
      answerOptions: [
        {
          answerText: "Massa Equatorial Continental, Massa Polar Atlântica.",
          isCorrect: false,
        },
        {
          answerText: "Massa Tropical Continental, Massa Polar Atlântica.",
          isCorrect: false,
        },
        {
          answerText: "Massa Equatorial Atlântica, Massa Tropical Continental",
          isCorrect: true,
        },
        {
          answerText:
            "Massa Equatorial Atlântica, Massa Equatorial Continental.",
          isCorrect: false,
        },
      ],
    },
    {
      questionText:
        "Marque a alternativa que explica o porquê da mudança de temperatura em áreas arborizadas.",
      answerOptions: [
        {
          answerText:
            "A sensação térmica diminui, pois em áreas arborizadas o vento flui com mais facilidade.",
          isCorrect: false,
        },
        {
          answerText:
            "A temperatura aumenta por causa da diminuição da umidade do local causada pela capilaridade.",
          isCorrect: false,
        },
        {
          answerText:
            "Os raios solares são irradiados pela vegetação oque causa um aumento na temperatura.",
          isCorrect: false,
        },
        {
          answerText:
            "A vegetação absorve os raios solares e aumenta a umidade do ar fazendo com que a temperatura diminua.",
          isCorrect: true,
        },
      ],
    },
    {
      questionText:
        "Marque a alternativa correta sobre os ventos alísios e contra-alísios",
      answerOptions: [
        {
          answerText:
            "Os ventos alísios são ventos úmidos dos trópicos para os polos.",
          isCorrect: false,
        },
        {
          answerText:
            "Os ventos contra-alísios são ventos secos que sopram do Equador para os trópicos.",
          isCorrect: true,
        },
        {
          answerText:
            "A Zona de Convergência Intertropical(ZCIT) é uma área na região dos trópicos que sofre ação dos ventos alísios o ano inteiro.",
          isCorrect: false,
        },
        {
          answerText:
            "Os ventos alísios são ventos úmidos que sopram dos trópicos para o Equador de oeste para leste.",
          isCorrect: false,
        },
      ],
    },
    {
      questionText: "Qual a forma de relevo mais predominante no Brasil?",
      answerOptions: [
        {
          answerText: "Planícies",
          isCorrect: false,
        },
        {
          answerText: "Bacias Hidrográficas",
          isCorrect: false,
        },
        {
          answerText: "Planaltos",
          isCorrect: true,
        },
        {
          answerText: "Dobramentos Modernos",
          isCorrect: false,
        },
      ],
    },
  ];

  const [currentQuestion, setCurrentQuestion] = useState(0);
  const [showScore, setShowScore] = useState(false);
  const [score, setScore] = useState(0);
  const [answerClick, setAnswerClick] = useState("a_btn");

  const handleAnswerOptionClick = (isCorrect) => {
    if (isCorrect) {
      setScore(score + 1);
      setAnswerClick("correct");
    } else {
      setAnswerClick("incorrect");
    }

    const nextQuestion = currentQuestion + 1;
    if (nextQuestion < questions.length) {
      setCurrentQuestion(nextQuestion);
    } else {
      setShowScore(true);
    }
  };
  function reload() {
    window.location.reload();
  }

  return (
    <main id="main1">
      <div className="app">
        {showScore ? (
          <div className="score-section">
            <h2>Parabéns!</h2>
            <br />
            Sua pontuação foi {score} de {questions.length}
            <button class="btn" onClick={reload}>
              Recomeçar
            </button>
          </div>
        ) : (
          <>
            <div className="question-section">
              <div className="question-count">
                <span>Questão {currentQuestion + 1}</span>/{questions.length}
              </div>
              <div className="question-text">
                {questions[currentQuestion].questionText}
              </div>
            </div>
            <div className="answer-section">
              {questions[currentQuestion].answerOptions.map((answerOption) => (
                <button
                  className={answerClick}
                  onClick={
                    () => handleAnswerOptionClick(answerOption.isCorrect)

                    // resposta correta - verde

                    // resposta incorreta - vermelho no botão clicado
                  }
                >
                  {answerOption.answerText}
                </button>
              ))}
            </div>
          </>
        )}
      </div>
    </main>
  );
}
